.TermAndCondition-container {
    max-width: 800px;
    margin: 0 auto;
    /* padding: 20px; */
    font-family: "Arial", sans-serif;
  }
  .PrivacyPolicyPageTitle {
    color: #000;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 2rem;
    line-height: 10px;
  }
  .TermAndConditionHeaderStyle {
    width: 100%;
    height: 40px !important;
    border-bottom: 1px solid #c7c4c4;
  }
  .TermAndConditionHeaderStyle {
    font-size: 24px;
    line-height: 24px;
    font-weight: 700;
    margin: 0;
    color: #000;
    padding-left: 180px;
  }
  .TermAndConditionSecondDiv {
    padding-left: 180px;
    width: 70%;
  }
  .TermAndConditionheading {
    font-size: 40px;
    line-height: 60px;
    font-weight: 700;
    color: #000;
  }
  .TermAndConditionContentStyle {
    color: #000;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 16px;
    line-height: 28px;
  }
  .TermAndConditionSubHeading {
    font-size: 32px;
    line-height: 48px;
    font-weight: 700;
    color: #000;
  }
  .TermAndConditionSubSubHeading {
    font-size: 24px;
    line-height: 36px;
    font-weight: 700;
    color: #000;
  }
  