a.social {
  margin: 0 1rem;
  transition: transform 250ms;
  display: inline-block;
}

a.social:hover {
  transform: translateY(-2px);
}

a.youtube {
  color: #eb3223;
}

a.facebook {
  color: #4968ad;
}

a.twitter {
  color: #49a1eb;
}

a.instagram {
  color: black;
}

.form-control {
  position: relative;
  flex: 1 1 auto;
  width: 400px;
  min-width: 0;
  border-radius: 5px;
  box-shadow: none;
  border-color: #688cb4;
  height: auto;
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  appearance: none;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.checkbox {
  color: #475f7b;
  font: 14px "IBM Plex Sans" sans-serif;
}
label {
  padding: 0px 0px 0px 5px;
}
b {
  /* size: 40.38*18; */
  width: 40.38px;
  height: 18px;
  color: #ffa800;
}
a {
  border: none;
}
.btn2 {
  border-radius: 5px;
  -webkit-box-shadow: none;
  box-shadow: none;
  border: 1px solid transparent;
  cursor: pointer;
  line-height: inherit;
  padding: 8px 16px;
  font-size: 1.1rem;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  background-color: #8950fc;
  border-color: #8950fc;
  color: #ffffff;
}
.button {
  text-align: center;
}
#checkbox_id {
